import queryString from 'query-string';
import cookies from 'js-cookie';
import qrMapping from '~global/variables/app-download-codes.json';
import getCdnBasePath from '~global/js/helpers/cdn';
import { isSmallScreen } from '~global/js/helpers/breakpoints';

export default function appDownloadData(type = 'qrCode') {
  if (type !== 'qrCode') {
    return {
      appStore: 'https://itunes.apple.com/app/id1288515333',
      googlePlay: 'https://play.google.com/store/apps/details?id=com.student.mobileapp',
    };
  }

  let utmSource = cookies.get('utm_source') || undefined;
  let utmMedium = cookies.get('utm_medium') || undefined;
  const gclid = cookies.get('gclid') || undefined;
  const device = isSmallScreen() ? 'Mobile' : 'Desktop';

  if (!utmSource && !utmMedium && window) {
    const queries = queryString.parse((window.location && window.location.search) || '');
    utmSource = queries.utm_source || undefined;
    utmMedium = queries.utm_medium || undefined;
  }

  if (gclid && gclid.length > 0) {
    utmSource = 'google';
    utmMedium = 'cpc';
  }

  const defaultKey = utmSource || utmMedium ? 'others' : 'default';
  const defaultResult = qrMapping.find(item =>
    item.source === defaultKey && item.medium === defaultKey);

  const result = qrMapping.find((item) => {
    if (item.source === 'baidu' && item.medium === 'cpc') {
      return item.source === utmSource && item.medium === utmMedium && item.device === device;
    }
    return item.source === utmSource && item.medium === utmMedium;
  }) || defaultResult;

  return {
    qrCode: `${getCdnBasePath()}bundles/microapp-home-page/images/public/app-download/${result.file}`,
    link: result.link,
  };
}



