import window from 'global/window';
import breakpoints from '~global/variables/layout.json';

export function isSmallScreen() {
  return window.innerWidth < breakpoints['medium-breakpoint'];
}

export function isMediumScreen() {
  return window.innerWidth >= breakpoints['medium-breakpoint'] && window.innerWidth < breakpoints['large-breakpoint'];
}

export function isLargeScreen() {
  return window.innerWidth >= breakpoints['large-breakpoint'];
}

export function isRetinaScreen() {
  const dpr = window.devicePixelRatio || window.screen.deviceXDPI / window.screen.logicalXDPI;
  return dpr > 1;
}



